#pomodoro {
  max-width: 800px; height: 100%; margin: auto; border: 1px solid black; border-radius: 20px; text-align: center; background-color: #9cc5c5; font-weight: bold; font-size: 16pt; font-family: "Comic Sans MS", cursive, sans-serif
}
#header {
  font-size: 20pt
}
button {
  border-style: solid; border-radius: 20px; background-color: #92a8d1; cursor: pointer; font-weight: bold; font-size: 16pt; font-family: "Comic Sans MS", cursive, sans-serif
}
.button:hover {
  background-color: brown
}

#time-left, #timer-label {
  font-size: 20pt
}